@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    margin-top: 4rem;
    background-color: v.$primario;
    @include m.tablet {
        margin-top: 8rem;
    }
    &__contenido {
        padding: 3rem 0;
        @include m.grid(1, 3rem);
        justify-items: center;
        @include m.desktop {
            @include m.grid(3, 3rem);
        }
    }
    &__logo {
        width: 85%;
        @include m.telefono {
            width: 30rem;
        }
        @include m.desktop {
            margin-top: -1rem;
        }
    }
    &__box {

    }
    &__heading {
        color: v.$blanco;
        text-align: center;
        padding-bottom: 1rem;
        margin: 0 auto 1rem;
        border-bottom: 1px solid v.$gris;
        width: fit-content;
    }
    &__nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;
        @include m.tablet {
            flex-direction: row;
            gap: 2rem;
        }
        @include m.desktop {
            flex-direction: column;
            gap: 1.5rem;
        }
    }
    &__enlace {
        color: v.$blanco;
        line-height: 1.5;
    }
    &__texto {
        color: v.$blanco;
        margin: 0;
        text-align: center;
        @include m.telefono {
            width: 30rem;
        }
        @include m.tablet {
            width: 60rem;
        }
        @include m.desktop {
            width: 30rem;
        }
    }
    &__copyright {
        color: v.$blanco;
        margin: 0;
        padding: 2rem;
        background-color: darken(v.$primario, 10%);
        text-align: center;
        font-size: 1.3rem;
        text-transform: uppercase;
        @include m.tablet {
            font-size: 1.4rem;
        }
    }
}