@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales-blog {
    padding: 3rem 1rem;
    @include m.tablet {
        padding: 0;
    }
    @include m.desktop {
        @include m.grid(3, 4rem);
    }
}
.testimoniales {
    margin-bottom: 5rem;
    @include m.desktop {
        grid-column: 1 / 2;
    }
    &__heading {
        @include m.heading();
        margin-bottom: 5rem;
        &--span {
            display: block;
        }
    }
    &__imagen {
        width: 20rem;
        height: auto;
        margin: 0 auto;
    }
    &__texto-box {
        background-color: v.$blanco;
        padding: 12rem 2rem 4rem;
        margin-top: -10rem;
    }
    &__nombre {
        font-size: 2.4rem;
        font-weight: 700;
        margin: 0;
        text-align: center;
    }
    &__texto {
        margin: 0;
        text-align: center;
        padding-top: 1rem;
        &::before {
            content: '';
            display: inline-block;
            width: 4rem;
            height: 4rem;
            background-image: url(../img/quote.svg);
            background-size: 4rem;
            background-repeat: no-repeat;
            background-position: 0 1rem;
            margin-right: 1rem;
        }
    }
}