@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    background-image: linear-gradient( to right, transparentize(v.$primario, .1 ) 0%, transparentize(v.$primario, .1 ) 100% ), url(../img/header_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    @include m.tablet {
        padding-bottom: 15rem;
        position: relative;
    }
    &__contenido {
        padding: 5rem 0;
        @include m.tablet {
            padding: 2rem 0 0;
        }
    }
    &__barra {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__logo-contenedor {
        margin: 0 auto 2rem;
        @include m.tablet {
            margin: 0 0 2rem;
        }
    }
    &__logo {
        width: 25rem;
        @include m.tablet {
            margin-top: .7rem;
        }
    }
    &__nav {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__enlace {
        color: v.$blanco;
        font-size: 1.8rem;
        text-align: center;
    }
    &__descripcion {
        margin-top: 10rem;
        @include m.tablet {
            width: 50rem;
        }
    }
    &__heading {
        color: v.$blanco;
        text-align: center;
        margin: 0;
        @include m.tablet {
            text-align: left;
        }
    }
    &__texto {
        display: none;
        @include m.tablet {
            display: block;
            color: v.$blanco;
        }
    }
    &__imagen {
        display: none;
        @include m.tablet {
            display: block;
            max-width: 35rem;
            border-top-left-radius: 2rem;
            border-bottom-left-radius: 6rem;
            position: absolute;
            bottom: -5rem;
            right: 0px;
        }
        @include m.desktop {
            max-width: 40rem;
        }
        @include m.desktopXL {
            max-width: 60rem;
        }
    }
}